.body {
    display: flex;
    flex-direction: column;
    /*max-height: calc(100vh - 170px);*/
    max-width: 600px;
    margin-top: 40px;
    max-height: 100vh;
}

.header {
    display: flex;
}

.ingredients {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-height: 500px;
    scroll-behavior: revert;
    position: sticky;
    overflow-y: scroll;
}

.ingredients::-webkit-scrollbar {
    box-sizing: border-box;
    width: 3px;
}

.ingredients::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

.ingredients::-webkit-scrollbar-thumb {
    background-color: #8585AD;
}

.container {
    display: grid;
    grid-template-columns:repeat(2, 240px);
    column-gap: 54px;
}

.nav {
    display: flex;
    flex-direction: row;
}

.btn {
    background-color: none;
    border: none;
    background: none;

    flex: none;
    order: 0;
    flex-grow: 1;
    margin: 20px 0px 0px;
}

.btn:hover {
    box-shadow: inset 0px -2px 0px #4C4CFF;
}

.tag {
    font-family: JetBrains Mono, sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 30px;
    display: flex;
    align-items: center;
    color: #F2F2F3;
    align-self: self-start;
    margin: 40px 0 0;
}

