.box {
    display: flex;
    flex-direction: column;
    align-items: center;

}

.price {
    font-family: JetBrains Mono, sans-serif;
    font-size: 8px;
    line-height: 8px;
    text-align: center;
    color: #B5FF3D;
    display: flex;
    flex-direction: row;
}

.name {
    font-family: JetBrains Mono, sans-serif;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: #F2F2F3;
    margin: 0;
    margin-top: 9px;
    text-decoration: none;
}

.number {
    font-family: JetBrains Mono, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 28px;
    line-height: 24px;
    text-align: center;
    color: #F2F2F3;
    flex: none;
    order: 0;
    flex-grow: 0;
    margin: 0px 8px;
}

.image {
    width: 240px;
    height: 120px;
}

.wrapper {
    position: relative;
}